html {
  height: 100%;
}

body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #333333;
  background-color: #ffffff;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.advantages {
  position: relative;
}
.advantages::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 37px;
  z-index: 5;
  background-repeat: no-repeat;
  background-image: url("../img/devider-advantages.svg");
}
@media (min-width: 321px) {
  .advantages::before {
    display: none;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.advantages__list--inner {
  padding: 30px 20px 20px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .advantages__list--inner {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }
}
@media (min-width: 768px) {
  .advantages__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
}

.advantages__item {
  text-align: center;
}
.advantages__list--inner .advantages__item {
  text-align: left;
}
@media (min-width: 768px) {
  .advantages__list--inner .advantages__item {
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .advantages__list--inner .advantages__item {
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 78px;
  }
}
@media (min-width: 768px) {
