    width: 33.3333333333%;
    min-height: 256px;
  }
}
.advantages__item--image {
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .advantages__item--image {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
  }
  .advantages__item--image:nth-child(2n) {
    flex-direction: row-reverse;
  }
  .advantages__item--image picture {
    display: flex;
    width: 66.6666666667%;
  }
}
.advantages__item--decor {
  position: relative;
  padding-left: 45px;
  margin-bottom: 30px;
}
.advantages__item--decor::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  left: -1px;
  top: -7px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
@media (min-width: 768px) {
  .advantages__item--decor::before {
    position: inherit;
    display: block;
    width: 75px;
    height: 75px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 768px) {
  .advantages__item--decor {
    padding: 65px 31px 60px;
    margin-bottom: 0;
  }
}
.advantages__item--house::before {
  background-image: url("../img/advantages-icon-house.svg");
}
.advantages__item--food::before {
