.main-nav--opened, .main-nav--nojs {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10;
}
@media (min-width: 768px) {
  .main-nav--opened, .main-nav--nojs {
    position: initial;
  }
}

.main-nav__toggle {
  background-color: #81b3d2;
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 23px;
  height: 20px;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover {
  background-color: rgba(129, 179, 210, 0.6);
}
.main-nav__toggle:active {
  background-color: rgba(129, 179, 210, 0.3);
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #ffffff;
}
.main-nav__toggle::before {
  top: 4px;
}
.main-nav__toggle::after {
  bottom: 4px;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--opened .main-nav__toggle::before {
  top: 8px;
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle:hover::before {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active::before {
  opacity: 0.3;
}
.main-nav--opened .main-nav__toggle::after {
  bottom: 8px;
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::after {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.3;
}

.main-nav__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.main-nav--opened .main-nav__list, .main-nav--nojs .main-nav__list {
  display: block;
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__list, .main-nav--nojs .main-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 768px) {
